<script>
import MainNotice from '@/components/main/MainNotice'
import Footer from '@/components/common/Footer'
import Swiper from '@/components/common/Swiper'
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
import  TermPopup2 from '@/components/common/TermPopup2'
export default {
	name: 'Main',
	mixins: [myMixin],
	components: {
		Footer,
		Swiper,
		MainNotice,
		TermPopup2
	},
	props: {},
	data() {
		return {
			secessionPw:'',
			password:'',
			myInfoFlag: false,
			obituary: {
				id: null,
				obituaryName: null,
				obituaryAge: null,
				obituaryAgeYn: null,
				obituarySex: null,
				obituarySexYn: null,
				obituaryMortuary: null,
				obituaryCode: null,
				obituaryDieYn: null,
				obituaryDieDate: null,
				obituaryEnterDate: null,
				obituaryCasketDate: null,
				obituaryCasketYn: null,
				obituaryReligionDate: null,
				obituaryReligionYn: null,
				obituaryEndDate: null,
				obituaryPlace: null,
				obituaryText: null,
				page: {
					page: this.$route.query.page ? this.$route.query.page : 0,
					size: this.$route.query.size ? this.$route.query.size : 8,
					sort: this.$route.query.sort ? this.$route.query.sort : 'regDate,desc',
				}
			},
			agreeFlag:false,
			endFlag: this.$route.query.endFlag ? this.$route.query.endFlag : 'all',
			searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
			searchType: this.$route.query.searchType ? this.$route.query.searchType : 'name',
			corpSearchTxt:''
		}
	},
	computed: {
		...mapGetters(['getMainNoticeList', "getObituaryCount", 'getObituaryReadCount', "memberInfo"]),
		corpFilterItems(){
		const corpListCopy = JSON.parse(JSON.stringify(this.getCodeList('CORP')))
		
		
		return corpListCopy.filter(item => {
			let itemTxt =  item.text
			itemTxt = itemTxt.replace(/ /g,"")
			let corpSearchTxt = this.corpSearchTxt.replace(/ /g,"")
			return itemTxt.toLowerCase().indexOf(corpSearchTxt.toLowerCase()) > -1
		})
		}
	},
	created() {
		this.$store.dispatch("REQUEST_MAIN")
	},
	mounted() {
		this.$store.commit('END_DEEMED');
		// $document ready 처럼 생성후 실행
		this.secessionPw = ''
		this.$nextTick(function() {
			this.allLoadingEvent()
		});
	},
	methods: {
		adAgreeBtnClickSubmit(val){
				this.agreeFlag = false
		this.$store.commit('END_DEEMED');
		
					const parameter ={
						member:{
							id:this.memberInfo.id,
							memberMarketingAgree:val
							}
					}
				this.$store.dispatch('REQUEST_CHANGE_MARKET_AGREE_MEMBER',parameter)
		},
		adAgreeBtnClick(){
				this.agreeFlag = true
				this.$store.commit('START_DEEMED');

		},
		popup(path){
			window.open(`${process.env.VUE_APP_BASEURL}${path}`);
		},
		secessionPwClick(){
			if(this.secessionPw === ''){
				alert('탈퇴하시려면 비밀번호를 입력해 주세요.')
				return
			}


			if(confirm("탈퇴하시면 복구가 불가능합니다. 정말로 탈퇴 하시겠습니까?")!=0){
				const parameter ={
					member:{
						id:this.memberInfo.id,
						memberPw:this.secessionPw
						}

				}
				this.$store.dispatch('REQUEST_SECESSION_MEMBER',parameter)
				
			} 
				this.secessionPw = ''


		},
    memberCorpChange(){
      
      let pwCheckFlag = false
      for(let i=0;i<this.getCodeList('CORP').length;i++){
        if(this.getCodeList('CORP')[i].value === this.memberInfo.memberCorpCode){
          if(this.getCodeList('CORP')[i].codeSubText === this.password){
            pwCheckFlag = true
          }
        }
      }
      if(!pwCheckFlag){
        alert("비밀번호를 확인해 주세요.");
        return
      }
      this.password = ''
      const parameter = {
        corpCodeId : this.memberInfo.memberCorpCode
      }
      this.$store.dispatch('REQUEST_MEMBER_CORP_UPDATE',parameter)
    },
		logout() {
			this.$store.dispatch('REQUEST_LOGOUT')
		},
		move(path) {
			this.$router.push(path)
		},
		requestSearch(flag) {
			if (flag !== null) {
				this.endFlag = flag
			}
			this.requestObituaryList()
		},
		requestObituaryList(param) {
			let currentPage = 0;
			if (param !== undefined && param !== null &&
				param.page !== undefined && param.page !== null) {
				currentPage = param.page - 1;
			}
			this.$router.push({
				path: '/obituaryList',
				query: {
					endFlag: this.endFlag,
					searchTxt: this.searchTxt,
					searchType: this.searchType,
					page: currentPage,
					size: this.obituary.page.size,
					sort: this.obituary.page.sort
				}
			})
		}
	}
}
</script>

